import React from "react";

const SelectBox = props => {
  let arr = [];
  let elements;
  if (props.selectType === "Person") {
    for (let i = 1; i <= props.numOfSelection; i++) arr.push(i);
    elements = arr.map((ele, index) => (
      <option>
        {ele} {index < 1 ? "Person" : "People"}
      </option>
    ));
  } else {
    let curr = 6;
    for (let i = 1; i <= props.numberOfTimePicker; i++) {
      let add = "";
      if (i % 2 !== 0) arr.push(curr + ":30");
      else {
        curr++;
        arr.push(curr + ":00");
      }
    }
    elements = arr.map((ele, index) => <option>{ele}</option>);
  }

  return <select className="selectBox">{elements}</select>;
};

export default SelectBox;
