import React from "react";
import "../style/grid.css";
import BtnNoBorder from "./BtnNoBorder";

const IntroCard = props => {
  const url = require("../img/introImg.jpg");
  return (
    <div className="col-1-of-3 introcard">
      <img className="IntroImg" src={url} alt="img"></img>
      <div className="IntroHeading">
        <h1 className="introNumber">{props.number}</h1>
        <h1>{props.heading}</h1>
      </div>
      <p className="IntroText">{props.text}</p>
      <BtnNoBorder />
    </div>
  );
};

export default IntroCard;
