import React, { Component } from "react";
import IntroCard from "../IntroCards";
import "../../style/IntroCards.css";

class IntroImg extends Component {
  state = {
    heading: ["Beautiful place", "Feel the coffee", "full taste"],
    text: [
      "Lorem ipsum dolor sit amet consectetur adipisicing elit. Magnam modi enim sed, culpa deleniti sint ipsum. Facilis officiis, error cumque odit odio, reiciendis, tempora nihil itaque cum animi ullam consequatur!"
    ]
  };

  render() {
    const arr = [1, 2, 3];
    const introcards = arr.map((ele, index) => (
      <IntroCard
        number={"0" + index}
        heading={this.state.heading[index]}
        text={this.state.text}
      />
    ));
    return <div className="grid">{introcards}</div>;
  }
}

export default IntroImg;
