import React, { Component } from "react";
import Text from "../order/Text";
import List from "./List";
import "../../style/menu.css";

class Menu extends Component {
  render() {
    const text = "FAVOURITE COFFEE FLAVOURS.";
    return (
      <div className="menu">
        <Text text={text} />
        <List />
      </div>
    );
  }
}

export default Menu;
