import React, { Component } from "react";
import SelectBox from "./SelectBox";
import Text from "./Text";
import "../../style/order.css";
import SelectGroup from "./SelectGroup";

class Order extends Component {
  render() {
    const numOfSelectPeople = 10;
    const numberOfTimePicker = 35;
    const text = "COFFEE BUILD YOUR BASE";
    return (
      <div className="order">
        <Text text={text} />
        <SelectGroup
          numOfSelectPeople={numOfSelectPeople}
          numberOfTimePicker={numberOfTimePicker}
        />
      </div>
    );
  }
}

export default Order;
