import React from "react";
import SelectBox from "./SelectBox";

const SelectGroup = props => {
  const style = {
    color: "#fff",
    margin: "0",
    lineHeight: "8vh"
  };
  return (
    <div className="order_submit">
      <SelectBox numOfSelection={props.numOfSelectPeople} selectType="Person" />
      <h2 style={style}> FOR </h2>
      <input type="date" value="2019-11-25" />
      <h2 style={style}> AT </h2>
      <SelectBox
        numberOfTimePicker={props.numberOfTimePicker}
        selectType="Time"
      />

      <button id="btnOrder">BOOK A TABLE</button>
    </div>
  );
};

export default SelectGroup;
