import React from "react";
import BtnWithBorder from "../btnWithBorder";

const MainSlider = props => {
  return (
    <div className="main_slider">
      <i className="fas fa-arrow-left" onClick={props.leftIndex}></i>
      <div class="main_slider_content">
        <img src={props.logoURL} className="logo" alt="Logo"></img>
        <h1 className="caption_big">{props.captionBig}</h1>
        <h3 className="caption_small">{props.captionSmall}</h3>
        <BtnWithBorder></BtnWithBorder>
      </div>
      <i className="fas fa-arrow-right" onClick={props.rightIndex}></i>
    </div>
  );
};

export default MainSlider;
