import React from "react";
import "../style/MenuList.css";

const MenuList = props => {
  return (
    <div className="drinkItem">
      <div className="menuDescription">
        <img src={props.url} alt="" className="drinkImg"></img>
        <h1 className="drinkName">{props.name}</h1>
        <p className="drinkDescription">{props.description}</p>
      </div>
      <div className="drinkPrice">
        <h2 className="price">{props.price}</h2>
        {props.new === 1 ? <button>New</button> : null}
      </div>
    </div>
  );
};

export default MenuList;
