import React from "react";
import MenuList from "../menuList";

const List = props => {
  const arr = [1, 2, 3, 4, 5, 6, 7, 8, 9, 1];
  const name = "CAFFE LATTE";
  const description = "Fresh brewed coffee and steamed milk";
  const price = "$2.95";
  const isNew = 0;
  const elements = arr.map((ele, index) => (
    <MenuList name={name} description={description} price={price} new={isNew} />
  ));
  return <div className="listContainer">{elements}</div>;
};

export default List;
