import React, { Component } from "react";
import BtnWithBorder from "../btnWithBorder";
import "../../style/SubBanner.css";

class SubBanner extends Component {
  render() {
    const url = require("../../img/coffee_logo_4.png");
    return (
      <div className="sub_banner">
        <div className="main_content">
          <img src={url} alt="Imagine" className="sub_banner_img"></img>
          <div className="mainText">
            <div className="text-heading">
              <h3>Application</h3>
              <h2>Use our application</h2>
            </div>
            <p className="main-para">
              Lorem ipsum, dolor sit amet consectetur adipisicing elit. Unde
              distinctio commodi molestiae provident ipsam animi facere,
              repudiandae repellendus doloribus! Ullam laudantium perferendis ex
              odit sint quae perspiciatis voluptas mollitia magnam?
            </p>
            <BtnWithBorder />
          </div>
        </div>
      </div>
    );
  }
}

export default SubBanner;
