import React from "react";

const Card = props => {
  return (
    <div className="col-1-of-4">
      <img src={props.url} alt="Img"></img>
      <p className="text">
        Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nihil nesciunt
        deleniti quas optio qui? Sed ducimus temporibus itaque! Facilis nobis
        doloribus ullam et cum beatae a ipsa quibusdam atque reprehenderit?
      </p>
    </div>
  );
};

export default Card;
