import React, { Component } from "react";
import "../../style/grid.css";
import Card from "../Card";
import "../../style/CoffeeMaker.css";

class CoffeeMaker extends Component {
  state = {
    url: [
      require("../../img/icon1.png"),
      require("../../img/icon2.png"),
      require("../../img/icon3.png"),
      require("../../img/icon4.png")
    ]
  };

  render() {
    const arr = [1, 2, 3, 4];

    const elements = arr.map((ele, index) => (
      <Card url={this.state.url[index]} />
    ));

    return <div className="grid coffeeMaker">{elements}</div>;
  }
}

export default CoffeeMaker;
