import React, { Component } from "react";
import "../../style/review.css";
import BtnNoBorder from "../BtnNoBorder";

class Reviews extends Component {
  state = {
    url: [
      require("../../img/slide_img_1.jpg"),
      require("../../img/slide_img_2.jpg"),
      require("../../img/slide_img_3.jpg"),
      require("../../img/slide_img_4.jpg")
    ],
    index: 0
  };

  changeImage() {
    let currIndex = this.state.index;
    currIndex++;
    currIndex %= this.state.url.length;
    let img = document.querySelector(".reviewImg");
    img.setAttribute("src", this.state.url[currIndex]);
    this.setState({ index: currIndex });
    setTimeout(() => {
      this.changeImage();
    }, 1000);
  }

  componentDidMount() {
    this.changeImage();
  }

  render() {
    return (
      <div className="reviews">
        <div className="mainTextReview">
          <h2 className="heading">TRY THE BEST COFFEE IN THE CITY</h2>
          <p className="reviews_para">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Dignissimos
            qui in, incidunt repellendus maiores perferendis ab necessitatibus
            ratione natus, accusamus pariatur possimus amet dolorem sit soluta
            rem. Consequatur, distinctio dignissimos.
          </p>
          <BtnNoBorder />
        </div>
        <div className="imgContainer">
          <img src="" alt="" className="reviewImg"></img>
        </div>
      </div>
    );
  }
}

export default Reviews;
