import React, { Component } from "react";
import SliderHeader from "./SliderHeader";
import MainSlider from "./MainSlider";
import "../../style/slider.css";
import "../../style/nav.css";
import "../../style/button.css";

class Slider extends Component {
  state = {
    navName: [
      "Home",
      "Menu",
      "Reservation",
      "Pages",
      "Blog",
      "Shop",
      "Elements"
    ],
    captionBig: [
      "Important Of Coffee",
      "Special Coffee Beans",
      "The Home Of Coffee"
    ],
    captionSmall: [
      "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Architecto quis maiores sint unde illum rem est facilis ut, consequuntur debitis possimus quisquam aspernatur voluptatibus expedita similique error delectus commodi! Quisquam."
    ],
    logoURL: [
      require("../../img/coffee_logo_1.png"),
      require("../../img/coffee_logo_2.png"),
      require("../../img/coffee_logo_3.png")
    ],
    indexCaption: 0
  };

  leftIndex() {
    console.log("clicked");
    let currIndex = this.state.indexCaption;
    const len = this.state.captionBig.length;
    currIndex--;
    if (currIndex < 0) currIndex += len;
    this.setState({ indexCaption: currIndex });
  }

  rightIndex() {
    let currIndex = this.state.indexCaption;
    const len = this.state.captionBig.length;
    currIndex++;
    currIndex %= len;
    this.setState({ indexCaption: currIndex });
  }

  render() {
    return (
      <div className="slider">
        <SliderHeader names={this.state.navName} />
        <MainSlider
          captionBig={this.state.captionBig[this.state.indexCaption]}
          captionSmall={this.state.captionSmall}
          leftIndex={this.leftIndex.bind(this)}
          rightIndex={this.rightIndex.bind(this)}
          logoURL={this.state.logoURL[this.state.indexCaption]}
        />
      </div>
    );
  }
}

export default Slider;
